@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Mono&display=swap);
#cursor-tag {
  width: 120px;
  height: 50px;
  background: white;
  box-shadow: 4px 4px #111;
  position: fixed;
  z-index: 1;
}

#cursor-tag p {
  padding: 0 10px;
  color: #111;
}
#scene {
  position: relative;
  width: 100%;
  height: 100%;
  background-image: url(/static/media/room.8dc5b7be.png);
  background-size: cover;
}
.item {
  display: block;
  position: absolute;
  cursor: -webkit-grab;
  cursor: grab;
  z-index: 1;
}

.item:active {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

#inventory-view {
  width: 100%;
  padding: 20px;
  display: flex;
  flex-direction: row;
}

.item-container {
  position: relative;
  width: 60px;
  height: 60px;

  margin-right: 20px;
}

.item.scene-item {
  max-height: 150px;
}

.item.inventory-item {
  width: auto;
  height: 100%;
}

.item.receptacle-item {

}

#terminal {
  background-image: url(/static/media/terminal.9dc3eac8.png);
  background-size: cover;
  width: 200px;
  height: 200px;
  position: absolute;
  top: calc(50% - 100px);
  left: calc(50% - 100px);
  cursor: pointer;
}

#terminal-view {
  position: relative;
  height: 100%;
  background-image: url(/static/media/terminal.180107bf.png);
  background-size: cover;
  padding: 50px;
}

#back-button {
  position: absolute;
  cursor: pointer;
  outline: none;
  border: none;
  width: 100%;
  bottom: 0;
  left: 0;
  max-width: 1000px;
  background: rgba(11, 11, 11, 0.1);
  color: #fff;
  font-weight: 800;
  height: 44px;
  -webkit-transition: height 0.2s, background 0.2s;
  transition: height 0.2s, background 0.2s;
}

#back-button:hover {
  height: 64px;
  background: rgba(11, 11, 11, 0.3);
}

#screen {
  background: #111;
  width: 80%;
  max-width: 750px;
  height: 295px;
  margin: 0px auto;
  padding: 20px 30px;
  font-size: 0.8rem;
  display: grid;
  grid-template-rows: 225px 65px;
  grid-gap: 10px 0;
}

#log {
  /* background: blue; */
  height: 225px;
  color: #e1e8e1;
  color: #cde9cd;
  overflow: scroll;
  padding-bottom: 10px;
  border-bottom: 1px dashed #cde9cd;
}

#screen form {
  width: 100%;
  display: grid;
  grid-template-columns: 25px auto;
  grid-template-rows: auto;
  align-items: start;
}

#screen form p {
  color: #cde9cd;
  font-size: 0.8rem;
  margin-top: 0px;

}

#screen input {
  display: block;
  width: 100%;
  background: inherit;
  border: none;
  outline: none;
  color: #cde9cd;
  font-size: 0.8rem;
}

#receptacle {
  position: relative;
  width: 100%;
  margin: 57px auto;
  height: 75px;
}


