/**** COMPONENT STYLES ****/

/**** MAIN ****/
* {
  box-sizing: border-box;
}

body {
  background: #111;
  margin: 0;
  padding: 0;
  font-family: 'IBM Plex Mono', monospace;
}

button, input {
  font-family: 'IBM Plex Mono', monospace;
  font-size: 1rem;
}

#root {
  width: 100%;
  height: 100vh;
  display: grid;
  align-items: center;
}

#app {
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
}

#scene-view,
#inventory-view {
  color: #eee;
  border: 2px solid #eee;
  margin-bottom: 15px;
}

#scene-view {
  height: 550px;
}

#inventory-view {

  height: 100px;
}
